<template>
    <div>
        <Vtable :buttons="buttons" :orders="list"/>
    </div>
</template>

<script>
import axios from 'axios'
import Vtable from './table.vue'
    export default {
        data(){
            return{
                buttons: [],
                list: [],
                reason: ''
            }
        },
        async mounted(){
            const orders = await axios.post('http://localhost:8080/token',{
                    token: localStorage.getItem('token')
            })
            if(orders.data)
            {
            this.list = orders.data[1].filter(order => order.status == 'skiped')
            this.contact = orders.data[0]  
            }else{
                localStorage.removeItem('token')
                window.location.reload()
            }
        },
        components:{
            Vtable
        }
    }
</script>

