<template>
    <div class="d-flex justify-content-center align-items-center">
        <div>
            <form action="." method="GET" @submit.prevent="Submit">
                <label class="label" v-if="label" for="">Не верные данные</label>
                <input v-model="name" class="form-control" placeholder="Имя" type="text">
                <input v-model="secname" class="form-control" placeholder="Фамилия" type="text">
                <input v-model="password" class="form-control" placeholder="Пароль" type="text">
                <button type="submit" class="btn btn-primary">Войти</button>
            </form>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
    export default {
        data(){
            return {
                name:'',
                secname: '',
                password:'',
                label: false
            }
        },
        methods:{
            async Submit(){
                    const responce = await axios.post('http://localhost:8080/authtification',{
                    name: this.name,
                    secondname: this.secname,
                    password: this.password
                })
                this.name = ''
                this.secname = ''
                this.password = ''

                if(responce.data.token)
                {
                    if(responce.data.admin)
                    {
                        localStorage.setItem('token', responce.data.token)
                        localStorage.setItem('admin', 'true')
                        this.$router.push('/crm')
                    }
                    else{
                        localStorage.setItem('token', responce.data.token)
                        this.$router.push('/master')
                    }
                }
                else{
                    this.label = true
                }
                
            },
           
        }
    }
</script>

<style scoped>
.label{
    color: red;
    font-size: 17px;
    font-weight: bold
}
</style>