<template>
    <div>
        <div class="container">
            <form   ref="allform" class="allform" @submit.prevent="SendMaster"  method="GET" action=".">
                <transition-group name="fade">
                    <div key="item" v-if="contopen" v-click-outside="onClickOutside" :class="[frombox,isshadow]">
                    <div>
                        <p id="conttext">Здравствуйте, оставьте Ваш телефон! Мастер перезвонит в течение 5 минут и рассчитает точную стоимость ремонта!</p>        
                    </div>
                        <div class="contactnbox">
                            <input v-model="masterName" class="form-control" placeholder="Имя мастера" type="text">
                            <input v-model="masterSecondName" class="form-control" placeholder="Фамилия мастера" type="text">
                            <input v-model="masterPassword" class="form-control" placeholder="Пароль для аккаунта" type="text">
                            <button  type="submit" class="font-weight-bold btn btn-info btn-lg fw-bold clr-1" style="margin-top:10px">Регистрировать</button>                    
                        </div>
                    </div>
                </transition-group>
            </form>
            <div class="row">
                <div class="col-sm-2 offset-sm-10">
                    <button @click="OpenCont" class="btn btn-primary">Регистрация</button>
                    <router-link to="/crm/orders" tag="button" class="btn btn-primary">Все заявки</router-link>
                    
                </div>
            </div>
            <div class="tool-box " @click="choosen = 'Холодильник'">
                <h2>Холодильник</h2>
            </div>
            <div class="tool-box"  @click="choosen = 'Морозильник'">
                <h2>Морозильник</h2>
            </div>
            <div class="tool-box"  @click="choosen = 'Стиральная машина'">
                <h2>Стиральная машина</h2>
            </div>
            <div class="tool-box"  @click="choosen = 'Газовая колонка'">
                <h2>Газовая колонка</h2>
            </div>
            <div class="tool-box"  @click="choosen = 'Водонагреватель'">
                <h2>Водонагреватель</h2>
            </div>
            <div class="tool-box"  @click="choosen = 'Газовая плита'">
                <h2>Газовая плита</h2>
            </div>
            <div class="tool-box"  @click="choosen = 'Варочная панель'">
                <h2>Варочная панель</h2>
            </div>
             <div class="tool-box"  @click="choosen = 'Посудомойка'">
                <h2>Посудомойка</h2>
            </div>
            <div class="tool-box"  @click="choosen = 'Котел'">
                <h2>Котел</h2>
            </div>
            <div class="tool-box"  @click="choosen = 'Кондиционер'">
                <h2>Кондиционер</h2>
            </div>
            <div v-if="choosen != ''">
                <div>
                    <h4>{{choosen}}</h4>
                </div>
                <hr>
                <div>
                    <form method="GET" action="." @submit.prevent="SendOrder">
                        <input v-model="number" type="text" class="form-control mb-4" placeholder="Номер телефона">
                        <input v-model="adress" type="text" class="form-control mb-4" placeholder="Адрес">
                        <input v-model="clientName" type="text" class="form-control mb-4" placeholder="Имя клиента">
                        <input v-model="defect" type="text" class="form-control mb-4" placeholder="Неисправность">
                        <select v-model="selMaster" class="custom-select mb-4" id="validationDefault04" required>
                            <option selected disabled value="">Мастер</option>
                            <option v-for="master in masters" :key="master.id" :value="GetMaster(master.name, master.secondname)">{{master.name}}  {{master.secondname}}</option>
                        </select>
                        <input v-model="comment" type="text" class="form-control mb-4" placeholder="Комментарий">
                        <button type="submit" class="btn btn-primary">Отправить заявку</button>
                    </form>
                    </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import vClickOutside from 'v-click-outside'
    export default {
        data(){
            return{
                axios: {},
                choosen: '',
                contopen: false,
                isshadow: 'some',
                labelStatus: false,
                frombox: 'frombox',
                masterName: '',
                masterSecondName: '',
                masterPassword: '',
                masters: [],
                number: '',
                adress: '',
                clientName: '',
                defect: '',
                selMaster: '',
                comment: '',
            }
        },
        directives: {
            clickOutside: vClickOutside.directive
        },
        methods:{
            reload(){
                window.location.reload()
            },
            GetMaster(name, secname){
                return name + secname
            },
            async SendMaster(){
                await axios.post('http://localhost:8080/register',{
                    name: this.masterName,
                    secondname: this.masterSecondName,
                    password: this.masterPassword
                })
                this.onClickOutside()
            },
            onClickOutside () {
                this.labelStatus = false;
                this.contopen=false;
            },
             OpenCont(){
                this.contopen=true;
                this.isshadow='darking'
                
            },
            async SendOrder(){
                    await axios.post('http://localhost:8080/order',{
                    technic: this.choosen,
                    number: this.number,
                    adress: this.adress,
                    clientname: this.clientName,
                    defect: this.defect,
                    selmaster: this.selMaster,
                    comment: this.comment,
                    date: (new Date).toLocaleDateString('ru-RU', {
                        year: "numeric",
                        month: "long",
                        day: "numeric"
                    })
                })
                this.choosen = ' '
                this.number = ' '
                this.adress = ' '
                this.clientname = ' '
                this.defect = ' '
                this.selmaster = ' '
                this.comment = ' '
                this.reload()
            }
        },
        created(){
            this.axios = axios
        },
        async mounted(){
           let data = await axios.get('http://localhost:8080/register')
           this.masters = data.data.filter(a=> a.name + a.secondname != 'СултанСултанмагомедов')
        }
    }
</script>

<style scoped>
.tool-box:hover{
cursor:pointer
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 1s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
  opacity: 0;
}
.darking {
  box-shadow: 0 0 0 1000px rgba(0, 0, 0,0.4);
}
.allform{
        position:fixed;
        z-index: 100;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        transition: 1s;
    }
    .frombox
    {
    border-radius:12px;
        align-items: center;
        background-color: white;
        display: flex;
        flex-direction: column;
        height: 350px;
        justify-content: space-around;
        left: 50%;
        padding: 30px;
        text-align: center;
        transition: 1s;
        width: 550px;
    }
#conttext{
    font-size: 19px;
}

</style>