import VueRouter from 'vue-router'
import navbar from './src/components/navigation.vue'
import home from './src/components/home.vue'
import auth from './src/components/auth.vue'
import crm from './src/components/crm.vue'
import master from './src/components/master.vue'
import newOrders from './src/components/newOrders.vue'
import allOrders from './src/components/allOrders.vue'
import doneOrders from './src/components/doneOrders.vue'
import skipedOrders from './src/components/skipedOrders.vue'
import inprocessOrders from './src/components/inprocessOrders.vue'


const routes = [
   
    {
      path: '/crm',
      component: crm,
      meta: {
        auth: true,
        admin: true
      }
    },
    {
      path:'/crm/orders',
      component: allOrders,
      meta: {
        auth: true,
        admin: true
      }
    },
    {
      path: '/auth',
      component: auth,
      meta:{
        authing: true
      }
    },
    {
      path: '/master',
      component: master,
      meta: {
        auth: true
      },
      children:[
        {
          path: 'orders/new',
          component: newOrders
        },
        {
          path:'orders/inprocess',
          component: inprocessOrders
        },
        {
          path: 'orders/done',
          component: doneOrders
        },
        {
          path: 'orders/skiped',
          component: skipedOrders
        }
      ]
    },
    {
      path: '*',
      component: navbar,
      children: [
      {
        path: '',
        component: home
      }
    ]
  },
  ]
  const router = new VueRouter({
      mode: 'history',
      base: '/',
    routes // сокращённая запись для `routes: routes`
  })

  router.beforeEach((to, from, next) => {
    if(to.matched.some(record=> record.meta.authing)){
      if(localStorage.getItem('token')){
        next('/master')
      }
      else{
        next()
      }
    }
    else{
      next()
    }
    if(to.matched.some(record=> record.meta.auth) && !to.matched.some(record=> record.meta.admin )){
      if(localStorage.getItem('token')){
        if(localStorage.getItem('admin')){
          next('/crm')
        }else{
          next()
        }
      }
      else{
        next('/auth')
      }
    }
    else if(to.matched.some(record=> record.meta.auth) && to.matched.some(record=> record.meta.admin )){
      if(localStorage.getItem('admin')){
        next()
      }
      else{
        next('/auth')
      }
    }
    else{
      next()
    }
  })

  export default router;