<template>
    <div>
        <Vtable :orders = "list" :buttons="buttons"/>
         <form   ref="allform" class="allform" @submit.prevent="SetStatus()"  method="GET" action=".">
                <transition-group name="fade">
                    <div key="item" v-if="contopen" v-click-outside="onClickOutside" :class="[frombox,isshadow]">
                    <div>
                        <p id="conttext">Введите причину отмены</p>    
                    </div>
                        <div class="contactnbox">
                            <input v-model="reason" class="form-control" placeholder="Причина" required type="text">
                            <button  type="submit" class="font-weight-bold btn btn-info btn-lg fw-bold clr-1" style="margin-top:10px">Завершить</button>                    
                        </div>
                    </div>
                </transition-group>
            </form>
    </div>
</template>

<script>
import vClickOutside from 'v-click-outside'
import Vtable from './table.vue'
import axios from 'axios'
    export default {
       
        data(){
            return {
                contopen: false,
                isshadow: 'some',
                labelStatus: false,
                frombox: 'frombox',
                id: 0,
                reason: '',
                list: [],
                contact:{},
                buttons: [
                    {
                        text: 'Принять',
                        method: async (id) => {
                            const res = await axios.post('http://localhost:8080/setstatus',{
                                name: this.contact.name,
                                secondname: this.contact.secondname,
                                id: id,
                                status: 'inprocess'
                            })
                            console.log(res)
                            window.location.reload()
                        }
                    },
                    {
                        text: 'Отменить',
                        method: (id) => {
                            this.id = id
                            this.OpenCont()
                            this.skip = true
                            this.finish = false
                        }
                    }
                ]
            }
        },
         methods:{
            async SetStatus(){
                    if(this.reason){
                        await axios.post('http://localhost:8080/setstatus',{
                        name: this.contact.name,
                        secondname: this.contact.secondname,
                        status: 'skiped',
                        id: this.id,
                        reason: this.reason 
                    })
                    window.location.reload();

                }
                
            },
             onClickOutside () {
                this.labelStatus = false;
                this.contopen=false;
            },
             OpenCont(){
                this.contopen=true;
                this.isshadow='darking'
                
            }
        },
        components:{
            Vtable
        },
       async mounted(){
            const orders = await axios.post('http://localhost:8080/token',{
                    token: localStorage.getItem('token')
            })

            if(orders.data){
                this.list = orders.data[1].filter(order => order.status == 'new')
                this.contact = orders.data[0]        
            }
            else{
                localStorage.removeItem('token')
                window.location.reload()
            }
                
        },
        directives: {
            clickOutside: vClickOutside.directive
        },
    }
</script>

