<template>
    <div>
        <Vtable :orders ="list" :buttons = "buttons"/>
    </div>
</template>

<script>
import Vtable from './table.vue'
import axios from 'axios'
    export default {
        components:{
            Vtable
        },
        data(){
            return{
                list: [],
                buttons: [],
            }
        },
        async mounted(){
            const orders = await axios.post('http://localhost:8080/token',{
                    token: localStorage.getItem('token')
            })
            if(orders.data)
            {
                this.list = orders.data[1].filter(order => order.status == 'done')
            }
            else{
                localStorage.removeItem('token')
                window.location.reload()
            }
        }
    }
</script>

