<template>
    <div>
        <table class="table table-bordered">
            <thead>
                <tr>
                <th scope="col">мастер</th>
                <th scope="col">Техника</th>
                <th scope="col">Номер клиента</th>
                <th scope="col">Адресс клиента</th>
                <th scope="col">Имя клиента</th>
                <th scope="col">Поломка</th>
                <th scope="col">Комментарий</th>
                <th scope="col">Статус</th>
                <th scope="col">Стоимость запчастей</th>
                <th scope="col">Стоимость работы</th>
                <th scope="col">Другие затраты</th>
                <th scope="col">Услуга</th>
                
                <th scope="col">Дата заявки</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="order, index in orders" :key="index">
                <td scope="row">{{order.name}} <br> {{order.secondname}}</td>
                <td>{{order.technic}}</td>
                <td>{{order.number}}</td>
                <td>{{order.adress}}</td>
                <td>{{order.clientname}}</td>
                <td>{{order.defect}}</td>
                <td>{{order.comment}}</td>
                <td>{{GetStatus(order.status)}}</td>
                <td>{{order.zapprice}}</td>
                <td>{{order.rabprice}}</td>
                <td>{{order.druprice}}</td>
                <td>{{order.service}}</td>
                <td>{{order.orderdate}}</td>
                </tr>
            </tbody>
            </table>
    </div>
</template>

<script>
import axios from 'axios'
    export default {
        data(){
            return{                  
                orders:[]
            }
        },
        async mounted()
        {
            const orders = await axios.get('http://localhost:8080/allorders')
            console.log(orders)
            this.orders = orders.data
        },
        methods:{
            GetStatus(state){
                if(state == 'done') return 'завершено'
                else if(state == 'skiped') return 'отменено'
                else if(state == 'inprocess') return 'в процессе'
                else if(state == 'new') return 'новая'



            }
        }
    }
</script>

