<template>
    <div>
        <Vtable :orders ="list" :buttons = "buttons"/>
        <form   ref="allform" class="allform" @submit.prevent="SetStatus"  method="GET" action=".">
                <transition-group name="fade">
                    <div key="item" v-if="contopen" v-click-outside="onClickOutside" :class="[frombox,isshadow]">
                    <div>
                        <p id="conttext">Введите затраты на выполнение заявки, и оказанную услугу</p>    
                    </div>
                        <div class="contactnbox">
                            <input v-model="zap" class="form-control" placeholder="Стоимость запчастей" type="text">
                            <input v-model="rab" class="form-control" placeholder="Стоимость работы" type="text">
                            <input v-model="dru" class="form-control" placeholder="Другие расходы" type="text">
                            <input v-model="ser" class="form-control" placeholder="Оказанная услуга" type="text">
                            <button  type="submit" class="font-weight-bold btn btn-info btn-lg fw-bold clr-1" style="margin-top:10px">Завершить</button>                    
                        </div>
                    </div>
                </transition-group>
            </form>
    </div>
</template>

<script>
import axios from 'axios'
import Vtable from './table.vue'
import vClickOutside from 'v-click-outside'
    export default {
         directives: {
            clickOutside: vClickOutside.directive
        },
        components:{
            Vtable
        },
        data(){
            return {
                contopen: false,
                isshadow: 'some',
                labelStatus: false,
                frombox: 'frombox',
                zap: '',
                rab: '',
                dru: '',
                ser: '',
                list: [],
                id: '',
                contact: {},
                buttons:
                [
                {
                    text: 'Завершить',
                    method: (id) => {
                        this.id = id
                        this.OpenCont()
                    }
                }
                ]
            }
        },
        methods:{
            async SetStatus(){
                await axios.post('http://localhost:8080/setstatus',{
                   name: this.contact.name,
                   secondname: this.contact.secondname,
                   id: this.id,
                   status: 'done',
                   zapprice: parseInt(this.zap.match(/\d+/)),
                   rabprice: parseInt(this.rab.match(/\d+/)),
                   druprice: parseInt(this.dru.match(/\d+/)),
                   service: this.ser
                })
                this.zap = ''
                this.rab = '',
                this.dru = ''
                this.onClickOutside()
                window.location.reload();
            },
           onClickOutside () {
                this.labelStatus = false;
                this.contopen=false;
            },
             OpenCont(){
                this.contopen=true;
                this.isshadow='darking'
                
            },
        },
        async mounted(){
            const orders = await axios.post('http://localhost:8080/token',{
                    token: localStorage.getItem('token')
            })
            if(orders.data)
            {
                this.list = orders.data[1].filter(order => order.status == 'inprocess')
                this.contact = orders.data[0]  
            }else{
                localStorage.removeItem('token')
                window.location.reload()
            }
        }
    }
</script>

<style>
.fade-enter-active, .fade-leave-active {
  transition: opacity 1s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
  opacity: 0;
}
.darking {
  box-shadow: 0 0 0 1000px rgba(0, 0, 0,0.4);
}
.allform{
        position:fixed;
        z-index: 100;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        transition: 1s;
    }
    .frombox
    {
    border-radius:12px;
        align-items: center;
        background-color: white;
        display: flex;
        flex-direction: column;
        height: 350px;
        justify-content: space-around;
        left: 50%;
        padding: 30px;
        text-align: center;
        transition: 1s;
        width: 550px;
    }
#conttext{
    font-size: 19px;
}

</style>